import React, {useEffect, useCallback} from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { LOAD_HASHTAG_POSTS_REQUEST } from '../reducers/post';
import PostCard from '../containers/PostCard';
const Hashtag = ({tag}) => {
    console.log("  tag:", tag);
    const dispatch = useDispatch();
    const { mainPosts, hasMorePost } = useSelector(state => state.post);
    const onScroll = useCallback(() =>{
        //window.scrollY:, clientHeight:  (), scrollHeight:   
        // console.log(window.scrollY, document.documentElement.clientHeight, document.documentElement.scrollHeight)
        if(window.scrollY +  document.documentElement.clientHeight > document.documentElement.scrollHeight -300){
          if(hasMorePost){ //     (    )
            dispatch({
              type: LOAD_HASHTAG_POSTS_REQUEST,
              lastId: mainPosts.length > 0 && mainPosts[mainPosts.length -1].id, //    (    ,       ) 
              data: tag,
            })
          }
        }
    }, [mainPosts.length, hasMorePost]);
    useEffect( ()=>{
        window.addEventListener('scroll', onScroll);
        return () =>{ //     
          window.removeEventListener('scroll', onScroll);
        }
    }, [mainPosts.length]);
    return(
        <div>
            {mainPosts ? mainPosts.map(c => (
                <PostCard key={c.id} post={c}></PostCard> 
            )): null} 
        </div>// (6.3 18:49) ?   ,  map undefined ,  ?
    );
};
Hashtag.propTypes = {
    tag: PropTypes.string.isRequired, 
}

Hashtag.getInitialProps = async (context) => {
    const tag = context.query.tag;
    console.log('hashtag props on hastag.js' , context.query.tag, context); //     , _app.js  
    context.store.dispatch({
        type: LOAD_HASHTAG_POSTS_REQUEST,
        data: tag,
    })
    return { tag };
};
export default Hashtag;