import React, { useState} from 'react';
import PropTypes from 'prop-types';
import { Icon } from 'antd';
import Slick from 'react-slick';
import styled from 'styled-components';

const Overlay = styled.div`
  position: fixed;
  z-index: 5000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
`;
// header  h1 , &      ( h1 ), 
const Header = styled.header`
  height: 44;
  background: white;
  position: relative;
  padding: 0;
  text-align: center;  

  & h1{
    margin: 0;
    fontSize: 17px;
    color: #333;
    line-height: 44px;
  }
`;


const SlickWrapper = styled.div`
  height: calc(100% - 44px);
  background: #090909;
`;
//        
const CloseBtn = styled(Icon)` 
 position: absolute;
 right: 0;
 top: 0;
 padding: 15;
 line-height: 14px;
 cursor: pointer;
`;
// div    
const Indicator = styled.div`
  text-align: center;
  & > div {
    width: 75;
    height: 30;
    line-height: 30px;
    borderRadius: 15;
    background: #313131;
    display: inline-block;
    text-align: center;
    color: white;
    font-size: 15px;
  }
`;
const ImgWrapper = styled.div`
padding: 32px;
text-align: center;
& img{
  margin: 0 auto; 
  max-height: 750px;
}
`;
const ImagesZoom = ({ images, onClose }) => {
  const [currentSlide, setCurrentSlide] = useState(0); //   

  return (
    <Overlay>
      <Header>
        <h1> </h1>
        <CloseBtn/>
      </Header>
      <SlickWrapper>
        <div>
          <Slick
            initialSlide={0} //    
            afterChange={slide => setCurrentSlide(slide)} //   index 
            infinite={false} //   1~4 > 4~1
            arrows
            slidesToShow={1} //  1 
            slidesToScroll={1} //  1 
          >
            {images.map((v) => {
              return (
                <ImgWrapper>
                  <img src={`http://localhost:3065/${v.src}`}/>
                </ImgWrapper>
              );
            })}
          </Slick>
          <Indicator>
            <div>
              {currentSlide + 1} / {images.length}
            </div>
          </Indicator>
        </div>
      </SlickWrapper>
    </Overlay>
  );
};

ImagesZoom.propTypes = {
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
  })).isRequired,
  onClose: PropTypes.func.isRequired,
};

export default ImagesZoom;